import React from 'react'
import './dailyweather.css'

const Dailyweather=({info,changeBtn})=> {
    return (
        <>
          <div className='col-auto col-md-4 col-lg-2'>
          <button className='btn border-white bg-white'onClick={changeBtn}>
              <div className='card mb-2' style={{width:'10rem'}}>
                  <div className='card-body'>
                      <h4 className='card-title text-center text-primary'>{info.day}</h4>
                      <div className='pic-resize'>
                      <img className='card-img-top' src={info.pic}/>
                      </div>
                      
                      <div className='row'>
                        <div className='col text-dark'>
                            {info.high}*C
                        </div>
                        <div className='col text-secondary'>
                            {info.low}*C
                        </div>
                      </div>
                      </div>
                      
                  </div>
                </button>
                </div>
        </>
    )
}

export default Dailyweather
