import React from 'react';
import {Route,Switch} from 'react-router-dom'
import './bootstrap.min.css'
import './App.css';
import cloud from './images/cloud.jpeg'
import sunny from './images/sunny.jpeg'
import rainy from './images/rainy.png'
import snowy from './images/snowy.jpeg'
import Dailyweather from './weather/dailyweather'
import Hourlyforecast from './weather/Hourly_forecast';
import PropTypes from 'prop-types'
class Weather_App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      day:'',
      hourly_data:[],
    Day_data:[{
      id:1,
      day:'Mon',
      pic:cloud,
      high:75,
      low:53
    },{
      id:2,
      day:'Tue',
      pic:rainy,
      high:23,
      low:55
    },
    {
      id:3,
      day:'Wed',
      pic:sunny,
      high:75,
      low:60
    },
    {
      id:4,
      day:'Thurs',
      pic:rainy,
      high:22,
      low:51
    },
    {
      id:5,
      day:'Fri',
      pic:snowy,
      high:31,
      low:58
    }],
    Monday_hour_data:[{hours:'09:00hrs',pic:sunny,high:73,low:39},{hours:'10:00hrs',pic:sunny,high:53,low:35},
                              {hours:'11:00hrs',pic:cloud,high:67,low:57},{hours:'12:00hrs',pic:cloud,high:49,low:32},
                              {hours:'13:00hrs',pic:cloud,high:63,low:32},{hours:'14:00hrs',pic:cloud,high:40,low:33}],
    Tuesday_hour_data:[{hours:'09:00hrs',pic:rainy,high:75,low:22},{hours:'10:00hrs',pic:rainy,high:60,low:40},
                              {hours:'11:00hrs',pic:cloud,high:61,low:35},{hours:'12:00hrs',pic:rainy,high:44,low:33},
                              {hours:'13:00hrs',pic:rainy,high:33,low:42},{hours:'14:00hrs',pic:rainy,high:50,low:30} ],
    Wednesday_hour_data:[{hours:'09:00hrs',pic:sunny,high:73,low:32},{hours:'10:00hrs',pic:sunny,high:63,low:32},
                              {hours:'11:00hrs',pic:sunny,high:73,low:35},{hours:'12:00hrs',pic:cloud,high:73,low:32},
                              {hours:'13:00hrs',pic:cloud,high:53,low:42},{hours:'14:00hrs',pic:sunny,high:70,low:36}],
    Thursday_hour_data:[{hours:'09:00hrs',pic:cloud,high:43,low:37},{hours:'10:00hrs',pic:sunny,high:63,low:20},
                              {hours:'11:00hrs',pic:cloud,high:43,low:30},{hours:'12:00hrs',pic:cloud,high:53,low:30},
                              {hours:'13:00hrs',pic:rainy,high:33,low:22},{hours:'14:00hrs',pic:rainy,high:70,low:36}],
    Friday_hour_data:[{hours:'09:00hrs',pic:cloud,high:70,low:30},{hours:'10:00hrs',pic:rainy,high:68,low:30},
                            {hours:'11:00hrs',pic:cloud,high:65,low:31},{hours:'12:00hrs',pic:rainy,high:75,low:55},
                            {hours:'13:00hrs',pic:snowy,high:44,low:23},{hours:'14:00hrs',pic:snowy,high:53,low:34}]

    }
  }

  getDaily_hour_forecast=(index)=>{
    if (index===0){
      this.setState({
        day:'Monday Hourly Data',
        hourly_data:this.state.Monday_hour_data
      })
    }
   else if (index===1){
      this.setState({
        day:'Tuesday Hourly Data',
        hourly_data:this.state.Tuesday_hour_data
      })
    }
   else if (index===2){
      this.setState({
        day:'Wednesday Hourly Data',
        hourly_data:this.state.Wednesday_hour_data
      })
    }
    else if (index===3) {
      this.setState({
        day:'Thursday Hourly Data',
        hourly_data:this.state.Thursday_hour_data
      })
    } else {
        this.setState({
          day:'Friday Hourly Data',
          hourly_data:this.state.Friday_hour_data
        })
    }
  }
  render(){
  return (<>
    <div className="container">
      <header className="row bg-primary text-white">
        <h1>Home</h1>
      </header>
      <hr/>
      <div className='row'>
      {this.state.Day_data.map((info,index)=><Dailyweather key={info.id} index={index} info={info} changeBtn={()=>this.getDaily_hour_forecast(index)}/>)}
      </div>
    <hr/>
    </div>
    <div className='container'>
    <div className='row bg-primary text-white'>
    {this.state.day}
    </div>
    </div>
    <div className='row'>
      <hr/>
    <Switch>
    <Route exact path='/' render={()=>this.state.hourly_data.map((info)=><Hourlyforecast key={info.id} info={info}/>)}/>
    <Route exact path='/mon' render={()=>this.state.Monday_hour_data.map((info)=><Hourlyforecast key={info.id} info={info} />)}/>
      <Route exact path='/tue' render={()=>this.state.Tuesday_hour_data.map((info)=><Hourlyforecast key={info.id} info={info} />)}/>
      <Route exact path='/wed' render={()=>this.state.Wednesday_hour_data.map((info)=><Hourlyforecast key={info.id} info={info} />)}/>
      <Route exact path='/thur' render={()=>this.state.Thursday_hour_data.map((info)=><Hourlyforecast key={info.id} info={info} />)}/>
      <Route exact path='/fri' render={()=>this.state.Friday_hour_data.map((info)=><Hourlyforecast key={info.id} info={info} />)}/>
    </Switch>
    </div>
    </>
  );
}
}
Dailyweather.propTypes={
  changeBtn:PropTypes.func.isRequired
}

export default Weather_App;
